.header {
    border: 1px solid #dfe4e8; 
    overflow: hidden;
    background-color: #fff;
    position: fixed; /* Set the navbar to fixed position */
    top: 0; /* Position the navbar at the top of the page */
    width: 100%;
    z-index: 1000;

    .logo {
      width: 230px;
      padding: 0 25px;
      margin: -40px 0;
      cursor: pointer;
    }
  
    .options {
      width: 50%;
      padding-right: 60px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
        cursor: pointer;
      }
    }
  }