@mixin respond($breakpoint) {
  @if $breakpoint == phone {
    @media only screen and (max-width: 37.5em) {
      @content;
    } //600px
  }
  @if $breakpoint == tab-port {
    @media only screen and (max-width: 56.25em) {
      @content;
    } //900px
  }
  @if $breakpoint == tab-land {
    @media only screen and (max-width: 75em) {
      @content;
    } //1200px
  }
  @if $breakpoint == big-desktop {
    @media only screen and (min-width: 112.5em) {
      @content;
    } //1800px
  }
}

.about-me {
  margin-top: 100px;

  @include respond(tab-port) {
    margin-top: 110px;
  }

  .content {
    width: 70%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px auto;
    font-family: "Montserrat", sans-serif;

    @include respond(tab-port) {
      width: 90%;
    }

    .content__header {
      text-align: center;
      padding-bottom: 20px;
      width: 100%;

      .title {
        font-size: 38px;

        &:after {
          content: ""; /* This is necessary for the pseudo element to work. */
          display: block; /* This will put the pseudo element on its own line. */
          margin: 0 auto; /* This will center the border. */
          width: 100px; /* Change this to whatever width you want. */
          padding-top: 6px; /* This creates some space between the element and the border. */
          border-bottom: 1px solid orange; /* This creates the border. Replace black with whatever color you want. */
        }

        @include respond(phone) {
          font-size: 33px;
        }
      }

      .profile-photo {
        width: 150px;
        height: auto;
        transform: rotate(15deg);
      }
    }

    .text-box {
      width: 100%;

      .bio {
        font-size: 20px;
        font-weight: 500;
        text-align: center;
      }

      ul {
        font-size: 24px;
        line-height: 1.3;
        background-color: #f7f8fa;
        padding: 20px;
        padding-left: 40px;

        .list_title {
          font-weight: 600;
          line-height: 2;
          font-size: 24px;
        }

        strong {
          font-weight: 500;
        }
      }
    }
  }
}
